.container {
  width: 290px;
  margin: 0 auto;
  padding: 0 15px;
}
@media (min-width: 768px) {
  .container {
    width: 708px;
    padding: 0 30px;
  }
}
@media (min-width: 1280px) {
  .container {
    width: 1000px;
    padding: 0 140px;
  }
}

body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  min-width: 320px;
  margin: 0;
  padding: 0;
}

.contrast-text--orange {
  color: #d67e34;
}
.contrast-text--lbrown {
  color: #bc9060;
}

.main-header {
  position: relative;
  background-image: url("../img/mobile/header-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  height: 800px;
}
@media (min-resolution: 2dppx) {
  .main-header {
    background-image: url("../img/mobile/header-bg@2x.png");
    background-size: cover;
  }
}

.main-header__intro-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 240px;
  padding-bottom: 304px;
}

.main-header__title {
  font-size: 28px;
  line-height: 34px;
  font-weight: 400;
  color: #d67e34;
  text-align: center;
  margin: 0;
}

.main-header__our-restaurant {
  font-size: 40px;
  line-height: 46px;
}

.main-header__eat-home {
  font-size: 15px;
  line-height: 20px;
  text-transform: uppercase;
  color: #ffffff;
  margin-top: 5px;
  margin-bottom: 0;
}

.main-header__menu-restaurant {
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #d67e34;
  width: 137px;
  height: 47px;
  margin-top: 45px;
}

.main-header__advantages {
  position: relative;
  bottom: 80px;
  display: flex;
  flex-direction: column;
  row-gap: 14px;
  width: 290px;
  margin: 0 auto;
  z-index: 5;
}

.main-header__advantage-block {
  position: relative;
  text-align: center;
  background-color: #ffffff;
  width: 264px;
  height: 156px;
  padding: 100px 13px 20px 13px;
  box-shadow: 10px 10px 40px 20px rgba(0, 0, 0, 0.1);
}
.main-header__advantage-block::before {
  content: "";
  position: absolute;
  top: 18px;
  left: 100px;
  background-image: url("../img/icons/dish.svg");
  background-repeat: no-repeat;
  width: 87px;
  height: 87px;
}

.main-header__advantage-title {
  font-size: 23px;
  line-height: 30px;
  font-weight: 400;
  margin-top: 0;
}

.main-header__advantage-description {
  font-size: 20px;
  line-height: 25px;
  color: #656363;
}

.main-nav {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
}

.main-nav__logo {
  display: inline-block;
  background-image: url("../img/icons/logo.svg");
  background-repeat: no-repeat;
  width: 45px;
  height: 31px;
}

.main-nav__button {
  display: inline-block;
  background-repeat: no-repeat;
  background-color: transparent;
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
}
.main-nav__button--open {
  width: 31px;
  height: 18px;
  background-image: url("../img/icons/menu.svg");
}
.main-nav__button--close {
  position: absolute;
  top: 25px;
  right: 20px;
  width: 23px;
  height: 23px;
  background-image: url("../img/icons/menu-close.svg");
}

.main-nav__menu {
  position: fixed;
  top: 0px;
  left: 0;
  right: 0;
  height: 95vh;
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.95);
  padding: 15px;
  transform: translateY(-100%);
  transition: 0.4s;
  z-index: 10;
}
.main-nav__menu--open {
  transform: translateY(0);
  transition: 0.4s;
}

.main-nav__nav-list {
  display: flex;
  flex-direction: column;
  row-gap: 15px;
  margin: 45px 0 0 0;
  padding: 0;
  list-style: none;
}

.main-nav__nav-link {
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 12vh;
  transition: 0.4s;
}
.main-nav__nav-link:hover {
  color: #656363;
  transition: 0.4s;
}

.main-nav__reservation {
  margin-top: 50px;
}

.main-nav__number {
  text-decoration: none;
  color: #000000;
}
.main-nav__number:hover, .main-nav__number:focus {
  text-decoration: underline;
}

.main-nav__feedback {
  color: #656363;
}

.main-nav__order {
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #d67e34;
  width: 157px;
  height: 47px;
  margin-top: 15px;
}

.story {
  text-align: center;
  margin-top: 825px;
}

.story__title {
  font-size: 40px;
  line-height: 46px;
  margin: 0;
}

.story__description {
  color: #656363;
  margin-top: 48px;
  margin-bottom: 96px;
}

.story__statistics {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  margin-bottom: 45px;
}

.story__statistic-percent {
  font-size: 20px;
  line-height: 27px;
  margin: 0;
}
.story__statistic-percent span {
  font-size: 40px;
  line-height: 54px;
}

.story__rent {
  background-image: url("../img/mobile/rent-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 20px 15px 30px 15px;
  margin-top: 51px;
}
@media (min-resolution: 2dppx) {
  .story__rent {
    background-image: url("../img/mobile/rent-bg@2x.png");
  }
}

.story__rent-title {
  font-size: 30px;
  line-height: 34px;
  font-weight: 400;
  color: #d67e34;
  margin-top: 0;
  margin-bottom: 21px;
}

.story__lunch-price {
  font-size: 24px;
  line-height: 33px;
  font-weight: 700;
  color: #ffffff;
}

.story__rent-link {
  font-size: 14px;
  line-height: 19px;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #d67e34;
  width: 139px;
  height: 43px;
  margin: 41px auto 0 auto;
}

.dishes__title {
  font-size: 40px;
  line-height: 46px;
  font-weight: 400;
  text-align: center;
  width: 176px;
  margin: 63px auto 68px auto;
}

.dishes__dish-list {
  position: relative;
  display: flex;
  flex-direction: column;
  row-gap: 87px;
  width: 290px;
  margin: 595px auto 126px auto;
  padding: 0 15px;
  list-style: none;
}
.dishes__dish-list::before {
  content: "";
  position: absolute;
  top: -546px;
  left: 0;
  width: 320px;
  height: 478px;
  background-image: url("../img/mobile/pizza-bg.png");
  background-repeat: no-repeat;
  background-size: contain;
}
@media (min-resolution: 2dppx) {
  .dishes__dish-list::before {
    background-image: url("../img/mobile/pizza-bg@2x.png");
  }
}

.dishes__dish-item {
  text-align: center;
  position: relative;
  display: flex;
  align-items: center;
  align-self: flex-end;
  width: 161px;
  height: 114px;
}
.dishes__dish-item::before {
  content: "";
  position: absolute;
  left: -129px;
  width: 130px;
  height: 114px;
  background-image: url("../img/mobile/burger.png");
  background-repeat: no-repeat;
  background-size: contain;
}
@media (min-resolution: 2dppx) {
  .dishes__dish-item::before {
    background-image: url("../img/mobile/burger@2x.png");
  }
}

.dishes__dish-name {
  font-size: 19px;
  line-height: 22px;
  margin: 0;
}
.dishes__dish-name--price {
  font-size: 23px;
  line-height: 27px;
}

.menu {
  background-image: url("../img/mobile/menu-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 62px 0 106px 0;
}
@media (min-resolution: 2dppx) {
  .menu {
    background-image: url("../img/mobile/menu-bg@2x.png");
  }
}

.menu__title {
  font-size: 50px;
  line-height: 60px;
  font-weight: 400;
  text-align: center;
  color: #ffffff;
  margin: 0 0 98px 0;
}

.menu__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.menu__item {
  text-align: center;
  position: relative;
  display: flex;
  flex-direction: column;
  width: 220px;
  height: 401px;
  background-color: #ffffff;
  margin-top: 20px;
  padding: 80px 35px 29px 35px;
}
.menu__item::before {
  content: "Recommended";
  font-size: 13px;
  line-height: 18px;
  text-transform: uppercase;
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 18px;
  background-color: #d67e34;
  padding: 9px 15px;
}
.menu__item::after {
  content: "";
  position: absolute;
  right: 28px;
  bottom: 205px;
  width: 100px;
  height: 100px;
  background-image: url("../img/icons/burger-price.svg");
}

.menu__item-title {
  font-size: 26px;
  line-height: 30px;
  font-weight: 400;
  margin: 18px 0 21px 0;
}

.menu__item-desc {
  margin-bottom: 17px;
}

.menu__order {
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 220px;
  height: 50px;
  background-color: #d67e34;
}

.reviews {
  background-color: #e5e4e4;
  padding: 11px 0 26px 0;
}

.reviews__description {
  font-size: 20px;
  line-height: 27px;
  font-style: italic;
  text-align: center;
  margin: 0 0 24px 0;
}

.reviews__human-info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.reviews__status {
  font-size: 20px;
  line-height: 27px;
  font-weight: 600;
  margin: 4px 0 5px 0;
}

.reviews__name {
  font-size: 20px;
  line-height: 27px;
}

.gallery__dishes-title {
  font-size: 40px;
  line-height: 46px;
  font-weight: 400;
  text-align: center;
  width: 154px;
  margin: 71px auto 82px auto;
}

.gallery__dishes-list {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
}